@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
/* CSS - Global */

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
}


:root{
	--color-white:#F8F1DA;
	--color-dark-cyan:#A1C5A2;
	--color-cyan-blue:#79B4B9;
	--color-blue:#198D93;
	--color-gray:#2F3343;
	--color-pink:#F54337;
	--not-override-table-size:1500px;
}

.body-text{
	color:#2F3343;
	color:var(--color-gray);
}

.body-container,body{
	background: url(/static/media/background.ba9a4f56.jpg) no-repeat top center fixed;
	background-size:cover; 

}

@media(max-width: 1500px){
	/*Adapta tamanho para qualquer tela de computador.
	Deve ser mudado se a tabela for mudada.
	As tabelas devem possuir um tamanho padr達o.*/
	.body-container{
		width: 1500px;
		width: var(--not-override-table-size);
	}
}

.header{
	margin-top: 0px;
	padding: 15px 15px 10px 15px;
	background-color: #79B4B9;
	background-color: var(--color-cyan-blue);
	font-size: 1.5em;
}

a{
	color: inherit; /* blue colors for links too */
  text-decoration: inherit;
}

.header>a{
	display: inline;
	padding-right: 40px;
}

@media(max-width: 650px){
	.sub-header>ul>li{
		display: block;
		padding-bottom: 15px;
}
}

.separator{
	margin:0px;
	color:#F8F1DA;
	color:var(--color-white);
}
.logo{
	width: 80px;
	float:left;
}
h3{
	/*Essa margem n達o pode ser mudada, se n達o a figura n達o fica legal*/
	color:#2F3343;
	color:var(--color-gray);
	margin-top: 30px;
}
*{
  font-family: 'Raleway';
	margin: 0px;
}

table{
	text-align: center;
}

.link-table,a{
	color: #2F3343;
	color: var(--color-gray);
}

td,th{
	position: -webkit-sticky;
	position: sticky;
	color:#2F3343;
	color:var(--color-gray);
	font-size: 30px;
	padding: 1rem 2rem;
	border-bottom-style: solid;
	font-size: 22px;
	min-width:100px;
	max-width:300px;
}

.wrapper{

	height: 500px;
	position: relative;
  overflow: auto;

}

hr{
	clear: both;
}

table{
	position: absolute;
	text-align: center;
	border-collapse: collapse;
	width: 100%;
}

select{
	height: 52px;/*+12*/
}
input{
	word-break: break-word;
	height: 40px;
}
.voltar-pequeno{
  width: 40px;
  vertical-align: middle;
}
/* CSS - Components*/

.card-input{
	display: grid;
	margin-bottom: 20px;
}

.card-container{
	width: 300px;
	margin: 5% auto;
	border-radius: 20px;
	background-color: #79B4B9;
	background-color: var(--color-cyan-blue);
	padding: 20px;
}
.card-container > label,
.card-container > p,
.card-container > a{
	color:#2F3343;
	color:var(--color-gray);
}
label{
	color:#2F3343;
	color:var(--color-gray);
}
.card-input >input{
	
}

.button-link{
	width: 100%;
	margin-top: 40px;
}

.body-container > div{

}

.card-container-table{
  width: 100%;
  padding: 0px;
  text-align: right;
}

.card-container-table>h2{
  text-align: center;
  padding: 20px;
}

.card-container-table>hr{
  margin: 0px 20px;
}

.card-container-table>.card-input{
  display: block;
  padding: 20px 20px 0px 20px;
}
.card-container-table>.card-input>input{
  width: 300px;
  margin-left: 10px;
}


.card-container-paciente{
  width: 400px;
}

.card-container-paciente > div > div > a > img{
  width: 100px;
}

.card-container-paciente >.flex-icons{
  display: flex;
}

.card-container-paciente >div> div{
  text-align: center;
  margin: 0px 5%;
}

.card-container-paciente>h2{
  text-align: center;
  padding: 20px;
}

.card-container-paciente>hr{
  margin-bottom: 20px;
  padding:0px;
}

.float-right{
  float: right;
}

#prescrever,#historico{
  display: inline-block;
}
#prescrever>img{
  display: block;
}
.img-input{
  text-align: left;
  float: left;
}

.img-input>a>label{
  vertical-align: middle;
  margin-left: 10px;
}
.card-container-table>div>.card-input{
  display: block;
  padding: 20px 20px 0px 20px;
}
.card-container-table>div>.card-input>input{
  width: 300px;
  margin-left: 10px;
}
.card-container-prescricao{
  width: 600px;
}


.prescricao-icons{
  margin-top:20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.on-remove{
  margin-bottom: 10px;
}

.prescricao-icons>div>a>img{
  width: 75px;
}

.prescricao-icons>div>figcaption{
  text-align: center;
}


.generated-by-array>.two-column,
.card-container-prescricao>.two-column{
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.two-column>.card-input>input{
  width: 250px;
}

.two-column>.card-input>select{
  width: 266px;
}
.card-container-prescricao > h2{
  text-align: center;
  padding: 20px;
}

.card-container-prescricao > hr{
  margin-bottom: 20px;
  padding:0px;
}
.modal>.card-container>h2{
  text-align: center;
  padding: 20px;
}

.modal>.card-container{
  padding-left: 10px;
  border-radius: 0px;
}

.modal>.card-container-table>table{
  margin-bottom: 10px;
}


.card-input-group{
  display: flex;
  text-align: left;
  justify-content: center;
  margin: 15px 0px 30px;
}
.card-input-group>.card-input{
  margin: 0px 15px;
}
.modal-button{
  margin: 10px 20px;
}

